.body{
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0%;
    left: 0%;
}

.login-form{
    position: fixed;
    top: 30%;
    left: 30%;
    margin-top: 0%;
    margin-left: 0%;
    display: flex;
}

.login-form-buttons{
    display: flex;
    justify-content: space-between;
}
.login-form-texts {
    display: grid;
    grid-template-columns: 100%;
    justify-items: center;
}