
.top-bar{
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0px;
    left: 0px;
    height: 80px;
    width: 100%;
    background-color: var(--clr-primary-0);
}

.top-bar-search-bar{
    justify-self: start;
    width: 40%;
    margin-top: .5%;
    margin-bottom: .5%;
    margin-left: 1%;
}